.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

body {
  overflow-x: hidden;
  margin: 0;
  font-family: "BloggerSans", "Lucida Console", "Monaco", sans-serif;
  font-weight: 300;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote {
  margin: 0;
}
