.page-main {
  width: 100%;
}

.page-main__back {
  margin-top: -1px;
  background-color: #161c36;
}

.page-main__wrapper-header {
  background: url("../img/globe-back-mobile@1x.jpg") no-repeat 0 -51px;

  @media (min-resolution: $retina-dppx) {
    background: url("../img/globe-back-mobile@2x.jpg") no-repeat 0 -51px cover;
  }
}

@media (min-width: $tablet-width) {
  .page-main__wrapper {
    height: 890px;
    background: url("../img/traveller-illustration-tablet@1x.png") no-repeat 71.8% 303px, url("../img/globe-back-tablet@1x.jpg") no-repeat -356px 282px #182044;

    @media (min-resolution: $retina-dppx) {
      background: url("../img/traveller-illustration-tablet@2x.png") no-repeat 71.8% 303px, url("../img/globe-back-tablet@2x.jpg") no-repeat -356px 282px cover #182044;
    }
  }

  .page-main__back {
    margin-top: -1px;
    padding-top: 101px;
    background-color: #182044;
  }

  .page-main__wrapper-header {
    background: none;
    display: flex;
    flex-direction: column;
    width: $tablet-wrapper;
    margin: 0 auto;
  }
}

@media (min-width: $desktop-width) {
  .page-main {
    display: flex;
    flex-direction: column;
  }

  .page-main__back {
    padding-top: 0;
  }

  .page-main__wrapper {
    height: 632px;
    background: url("../img/traveller-illustration-desktop@1x.png") no-repeat 604px -9px, url("../img/globe-back-desktop@1x.jpg") no-repeat -182px -117px #161c36;

    @media (min-resolution: $retina-dppx) {
      background: url("../img/traveller-illustration-desktop@2x.png") no-repeat 604px -9px, url("../img/globe-back-desktop@2x.jpg") no-repeat -182px -117px cover #161c36;
    }
  }

  .page-main__wrapper-header {
    width: $desktop-wrapper;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
